* {
  font-family: 'Roboto';
}

/* Background Color */
.app-background {
  background-color: #FFFFFF;
}

/* For Highcharts to fit into the container */
.highcharts-container {
  width: 100% !important;
}
.highcharts-container > svg {
  width: 100% !important;
}

/* Sizing for switched pages */
.switch-wrapper {
  position: relative;
}
.switch-wrapper > div {
  position: absolute;
  width: 100%;
}

/* Skill Icon for React etc */
.skill-icon {
  margin: 7px;
  cursor: pointer;
  opacity: 0.7;
  filter: alpha(opacity=70);
  position: relative;
  top: 0px;
  transition: transform .2s ease-in-out;
}
.skill-icon:hover {
  opacity: 1.0;
  filter: alpha(opacity=100); /* For IE8 and earlier */
  transform: translate3d(0px, -10px, 0px);
}

/* Header animations */
.header-link {
  opacity: 0.7;
  filter: alpha(opacity=70);
  transition: opacity 0.33s ease-in-out;
}
.header-link:hover {
  opacity: 1.0;
  font-size: 1.6rem;
  filter: alpha(opacity=100); /* For IE8 and earlier */
  transition: opacity .15s ease-in-out, font-size 0.15s ease-in-out;
}

/* Contact Spinner */
#orbit-container {
  position: relative;
  height: 90vh;
}

#outer-orbit {
  position: absolute;
  left: 25%;
  top: 20%;
  width: 25vw;
  height: 25vw;
  /* border: 2px #4A437F dashed; */
  border-radius: 100%;
  animation: circle 200s infinite linear;
}
.outer-orbit-circles {
  position: absolute;
  height: 12vh;
  width: 12vh;
  animation: planet 200s infinite linear;
}
.orbit-icons {
  cursor: pointer;
  height: 12vh;
  width: 12vh;
  rotate: 90deg;
  opacity: 0.7;
  filter: alpha(opacity=70);
  transition: transform .2s ease-in-out;
}
.orbit-icons:hover {
  opacity: 1.0;
  filter: alpha(opacity=100); /* For IE8 and earlier */
  transform: scale(1.15);
}

/* ---------- Animation ---------- */
@keyframes circle { to { transform: rotate(-1turn); } }
@keyframes planet { to { transform: rotate(1turn); } }